<template>
    <div>
        <!-- 标题 -->
        <div class="title">
            <van-nav-bar title="选择专属你的户门"/>
        </div>
        <!-- 内容 -->
        <div class="content">
            <!-- 步骤条 -->
            <div class="stepbar">
                <van-steps :active="active">
                    <van-step>款式选择</van-step>
                    <van-step>锁体选择</van-step>
                    <van-step>选择户门</van-step>
                    <van-step>效果查看</van-step>
                </van-steps>
            </div>
            <!-- 图片展示 -->
            <div class="show_image">
                <img src="../../static/0.jpg" alt="" ref="Bigimg">
            </div>
            <!-- 选择部分 -->
            <div class="choose">
                <div class="style_select">
                    <p>款式选择</p>
                    <ul>
                        <li @click.prevent="styleAdd(0)" :class="[inde === 0 ? 'active': '']" id="Smallimg"> 
                            <img src="../../static/0.jpg" alt="" ref="one"> 双色款 
                        </li>
                        <li @click.prevent="styleAde(1)" :class="[inde === 1 ? 'active': '']" id="Smallimg"> 
                            <img src="../../static/1.jpg" alt="" ref="two"> 基础款 
                        </li>
                    </ul>
                </div>
                <!-- 锁芯选择 -->
                <div class="lock_select style_select">
                    <p>锁芯选择</p>
                    <p v-show="LockHide" style="color: #a2a1a1;font-size: 12px;margin-left: 20px;">请选择款式</p>
                    <ul v-show="LockShow">
                        <li v-for="(item,index) in LockList" :key="index" :class="[imke === index ? 'active': '']" @click.prevent="LockAdd(index)"> 
                            <img :src="item.img" alt="">  {{item.name}} 
                        </li>
                    </ul>
                </div>
            </div>
        </div>
        <!-- 底部 -->
        <div class="footer">
            <button :class="[dis === 0 ? 'disabled': '']" @click.prevent="Jump">查看效果图</button>
        </div>
    </div>
</template>

<script>
export default {
    data() {
        return {
            Stylelist:[
                {img: "http://127.0.0.1:8080/static/0.jpg", name: "双色款"},
                {img: "http://127.0.0.1:8080/static/1.jpg", name: "单色款"}
            ],
            LockList: [
                {img: 'http://www.olalocks.com/images/ola_plus_20.png', name: '普通款'},
                {img: 'http://www.olalocks.com/images/ola_plus_21.png', name: '霸王款'}
            ],
            inde: 3,                    // 款式样式当前索引
            imke: 3,                    // 锁芯样式当前索引
            dis: 0,                     //
            active: 2,                  // 顶部步骤条当前索引
            LockShow: false,            // 锁芯选择显示隐藏
            LockHide: true,             // 锁芯选择显示隐藏
        }
    },
    created() {
        
    },
    methods: {
        styleAdd(id){
            this.inde = id
            // console.log(this.inde)
            if(this.inde == 0 || this.inde == 1){
                this.LockShow = true
                this.LockHide = false
                this.$refs.Bigimg.src = this.$refs.one.src
            }else{
                this.LockShow = false
                this.LockHide = true
            }
            this.$refs.Bigimg.src = this.$refs.one.src
            console.log(this.$refs.one.src)
            // console.log(this.$refs.Bigimg.src)
        },
        styleAde(id){
            this.inde = id
            // console.log(this.inde)
            if(this.inde == 0 || this.inde == 1){
                this.LockShow = true
                this.LockHide = false
                this.$refs.Bigimg.src = this.$refs.two.src
            }else{
                this.LockShow = false
                this.LockHide = true
            }
        },
        LockAdd(id){
            this.imke = id
        },
        Jump(){
            if(this.dis == 1){
                this.$router.push('/styleselect')
            }
        }
    },
    updated() {
        if(this.inde == 1 && this.imke == 0 || this.imke == 1){
            this.dis = 1
        }else if(this.inde == 0 && this.imke == 0 || this.imke == 1){
            this.dis = 1
        }
    },
}
</script>

<style lang="less" scoped>
.title{
    width: 100%;
    height: 50px;
    line-height: 50px;
    border-bottom: 1px solid #ccc;
    display: flex;
    .van-nav-bar{
        width: 100%;
    }
}
.content{
    .stepbar{
        width: 90%;
        margin: 5px auto;
    }
    .show_image{
        img{
            width: 50%;
            margin: 0 auto;
            display: block;
            padding: 10px;
            background: rgb(250,250,248);
        }
    }
    .choose{
        margin-top: 15%;
        .active{
            border: 1px solid rgb(7, 193, 96);
            color: rgb(7, 193, 96);
        }
        .style_select{
            padding-left: 5%;
            width: 90%;
            p{
                margin-bottom: 10px;
                font-weight: bold;
            }
            ul{
                width: 100%;
                height: 100%;
                display: inline-block;
                li{
                    font-size: 13px;
                    padding: 5px 0 5px 10px;
                    background: #f8f8f8;
                    width: 30%;
                    border-radius: 8px;
                    display: flex;
                    line-height: 21px;
                    float: left;
                    margin-right: 10%;
                    margin-left: 6%;
                    img{
                        width: 20px;
                        height: 20px;
                        background-size: 100% 100%;
                        margin-right: 10px;
                    }
                }
            }
        }
        .lock_select{
            margin-top: 20px;
            li{
                i{
                    background: url(http://www.olalocks.com/images/ola_plus_20.png);
                }
            }
        }
    }
}
.footer{
    position: fixed;
    bottom: 0;
    width: 100%;
    padding: 10px;
    background: #ededed;
    button{
        width: 90%;
        display: block;
        color: #fff;
        font-size: 18px;
        background: rgb(7, 193, 96);
        text-align: center;
        line-height: 35px;
        border-radius: 20px;
    }
    .disabled{
        color: #ccc;
    }
}
</style>


